.btn{
    background-color: var(--colorBackButton);
    box-shadow: 0px 1px 0px 0px var(--colorShadowButton);
    color: #FFF;
    text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.25);
    font-size: 18px;
    font-weight: 400;
    line-height: 20px;
    padding: 10px 18px;
    cursor: pointer;
}