//.invalid-input
.invalid-input {
    border: 2px solid var(--colorError);
}

//.invalid-text
.invalid-text {
    color: var(--colorError);
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    margin: 4px 0 0 390px;
}

//.valid-input
.valid-input {
    border: 2px solid var(--colorBackButton);
}